.table-new__container {
  border-radius: $base-border-radius;
  box-shadow: $box-shadow;
  margin: 0 auto $base-unit;
  width: calc(100% - #{$base-unit * 2});

  @include mq($max-width: $screen-m) {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }

  @include mq($min-width: $screen-m) {
    overflow: hidden;
  }

  @include mq($min-width: $screen-xl) {
    width: 80%;
    max-width: $base-unit * 54;

    .previous-slide & {
      width: calc(100% - #{$base-unit * 2});
    }
  }
}

// table,
.table-new {
  background: $bg-color-1;
  // background-clip: padding-box;
  width: 100%;
  min-width: $base-unit * 25; // 640px
}

// tr
.table-new__row {}

// td, th,
.table-new__cell {
  background: $color-bg-0;
  // background-clip: padding-box;
  border-top: 1px solid $color-bg--1;
  padding: $base-unit * .5;
  vertical-align: top;

  .table-new__cell--input {
    width: 100%;
    height: 100%;
    padding: 0 ($base-unit / 2);
    border: 0;
  }
}

// th
.table-new__cell--header {
  background: $color-bg--1;
  // background-clip: padding-box;
  border-top: 1px solid $color-bg--2;
  font-weight: normal;

  thead & {
    border-top: 0;
    border-bottom: 1px solid $color-bg--1;
    vertical-align: bottom;
  }
}
